<template>
  <a-form-model :model="form" layout="vertical" style="display: flex">
    <div style="margin-right: 20px; display: flex; align-items: center">
      <a-upload
        name="avatar"
        list-type="picture-card"
        class="avatar-uploader"
        :show-upload-list="false"
        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
        :before-upload="beforeUpload"
        @change="handleChange"
      >
        <img
          v-if="form.avatar"
          :src="form.avatar"
          alt="avatar"
          class="avatar"
        />
        <div v-else>
          <a-icon :type="loading ? 'loading' : 'plus'" />
          <div class="ant-upload-text">用户头像</div>
        </div>
      </a-upload>
    </div>
    <div>
      <a-row :gutter="24">
        <a-col :span="8">
          <a-form-model-item label="用户名" :name="form.userName">
            <a-input v-model:value="form.userName" placeholder="请输入用户名" />
          </a-form-model-item>
        </a-col>

        <!-- 密码 -->
        <a-col :span="8">
          <a-form-model-item label="密码" name="password">
            <a-input v-model:value="form.password" placeholder="请输入密码" />
          </a-form-model-item>
        </a-col>
        <!-- 确认密码 -->
        <a-col :span="8">
          <a-form-model-item label="确认密码" name="confirmPassword">
            <a-input
              v-model:value="form.confirmPassword"
              placeholder="请输入确认密码"
            />
          </a-form-model-item>
        </a-col>

        <!-- 角色 -->
        <a-col :span="8">
          <a-form-model-item label="角色" name="role">
            <a-select v-model:value="form.role" placeholder="请选择角色">
              <a-select-option value="admin"> 管理员 </a-select-option>
              <a-select-option value="user"> 普通用户 </a-select-option>
            </a-select>
          </a-form-model-item>
        </a-col>
        <a-col :span="8">
          <a-form-model-item label="状态" :name="form.status">
            <!-- 单选框 激活 禁用 -->
            <a-radio-group v-model:value="form.status">
              <a-radio :value="true"> 启用 </a-radio>
              <a-radio :value="false"> 禁用 </a-radio>
            </a-radio-group>
          </a-form-model-item>
        </a-col>
      </a-row>
    </div>
  </a-form-model>
</template>
<script>
function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener("load", () => callback(reader.result));
  reader.readAsDataURL(img);
}
export default {
  props: {
    entity: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  data() {
    return {
      loading: false,
      form: this.entity,
    };
  },
  methods: {
    handleChange(info) {
      if (info.file.status === "uploading") {
        this.loading = true;
        return;
      }
      if (info.file.status === "done") {
        // Get this url from response in real world.
        getBase64(info.file.originFileObj, (imageUrl) => {
          this.imageUrl = imageUrl;
          this.loading = false;
        });
      }
    },
    beforeUpload(file) {
      const isJpgOrPng =
        file.type === "image/jpeg" || file.type === "image/png";
      if (!isJpgOrPng) {
        this.$message.error("You can only upload JPG file!");
      }
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error("Image must smaller than 2MB!");
      }
      return isJpgOrPng && isLt2M;
    },
  },
};
</script>
<style scoped>
.avatar-uploader > .ant-upload {
  width: 128px;
  height: 128px;
}
.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}

.avatar {
  width: 128px;
  height: 128px;
}
</style>
